body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.section-hero {
  padding-top: 4rem;
  padding-bottom: 5.2rem;
  padding-left: 5.2rem;
  padding-right: 5.2rem;
  background-color: #d7b59c;
  height: 90vh;
}

.hero {
  max-width: 130rem;
  margin: 0 auto;
  padding: 0 3.2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 9.6rem;
  gap: 9.6rem;
  align-items: center;
}

.hero-description {
  font-size: 2rem;
  line-height: 1.6;
  margin-bottom: 4.8rem;
}

.hero-img {
  width: 110%;
}

.heading-primary {
  font-size: 3rem;
  font-weight: bold;
}

.heading-secondary {
  font-size: 2.4rem;
}

.btn {
  font-size: 1.6rem;
  margin-top: 3rem;
  border-radius: 0px;
}

.data-card {
  border-radius: 0px;
  box-shadow: 5px 5px;
  margin-bottom: 10rem;
}

.btn {
  border-radius: 0px;
}

.card-body {
  display: flex;
  justify-content: space-between;
}

.ar {
  text-align: right;
}

.bold {
  font-weight: bold;
}

.left-con {
  max-width: 60rem;
}

color-s {
  padding: 3rem 0;
}

.j-container {
  margin-top: 0rem !important;
  padding: 0 10rem;
  background-color: #d7b59c;
}

.j-title {
  color: #000;
  font-size: 2.4rem;
  font-weight: bold;
  padding: 2rem 0rem;
}

